
import * as admin from 'firebase-admin';

// This utility ensures that the Firebase Admin SDK is initialized only once
// across all server-side requests in a serverless environment.

interface FirebaseAdmin {
  auth: admin.auth.Auth;
  firestore: admin.firestore.Firestore;
  storage: admin.storage.Storage;
}

if (!admin.apps.length) {
  try {
    admin.initializeApp({
      credential: admin.credential.applicationDefault(),
      storageBucket: process.env.NEXT_PUBLIC_FIREBASE_STORAGE_BUCKET,
    });
    console.log('Firebase Admin SDK initialized successfully.');
  } catch (error) {
    console.error('Firebase Admin SDK initialization error:', error);
  }
}

const auth = admin.auth();
const firestore = admin.firestore();
const storage = admin.storage();

export function getFirebaseAdmin(): FirebaseAdmin {
  return { auth, firestore, storage };
}
